	import {setToken, getToken ,setInspectionModal, getByKey ,setByKey} from '../../util/auth.js'
	import {savePatrolWorkText,getDictData} from '../../api/user.js'
	import {upload} from '../../api/upload.js'
	import {isNullOrEmpty,objIsNullOrEmpty,getUUID} from '../../util/stringUtil.js'
	import {isNetwork,upLoadText,upLoadPic} from '../../util/base.js'
    export default {
        data() {
            return {
                message: '',
                workCur: 0,
                gridCol: 4,
                gridBorder: false,
                cuIconList: [{
                    cuIcon: 'discoverfill',
                    color: 'orange',
                    badge: 0,
                    name: '风机巡检'
                }, {
                    cuIcon: 'ticket',
                    color: 'yellow',
                    badge: 0,
                    name: '物资审批'
                }, {
                    cuIcon: 'form',
                    color: 'cyan',
                    badge: 0,
                    name: '操作票管理'
                }, {
                    cuIcon: 'explore',
                    color: 'mauve',
                    badge: 0,
                    name: '检修管理'
                }, {
                    cuIcon: 'form',
                    color: 'green',
                    badge: 0,
                    name: '场内巡检'
                }
				],
				modalName: null,
				modalText:''
            }
        },
        onLoad(){
			this.upLoad()
			
        },
        methods:{
            workSelection(e){
                this.workCur = e.currentTarget.dataset.id;
                if(this.workCur==0){
                    uni.navigateTo({
                        url: 'inspectionStart'
                    });
                }
                if(this.workCur==1){
                    uni.navigateTo({
                        url: 'approvalMain'
                    });
                }
                if(this.workCur==2){
                    uni.navigateTo({
                        url: 'operationTicket'
                    });
                }
                if(this.workCur==3){
                    uni.navigateTo({
                        url: 'overhaul'
                    });
                }
				if(this.workCur==4){
				    uni.navigateTo({
				        url: 'overhaul'
				    });
				}
            },
			//模态框
			showModal() {
				this.modalName = 'DialogModal1'
			},
			hideModal() {
				this.modalName = null
			},
			upLoad(){
				var _this = this
				var checkDetail = getByKey('checkDetail')
				var jsonData = getByKey('jsonData')
				//判断网络状态并上传
				isNetwork(function(){
					if(!isNullOrEmpty(jsonData)){
						upLoadText(jsonData)
						setByKey('jsonData',null)//清空缓存
						if(!isNullOrEmpty(checkDetail)){
							upLoadPic(checkDetail)
							setByKey('checkDetail',null)//清空缓存
						}
						_this.modalText = '未上传的巡检任务保存成功'
						_this.showModal()
					}
				},function(){
					console.log(2)
					if(!isNullOrEmpty(jsonData)){
						_this.modalText = '有待上传的巡检任务，快去联网上传'
						_this.showModal()
					}
				});
				//加载下拉框数据
				getDictData().then(res =>{
					var patrolNum = res.patrolNum
					setByKey('patrolNum',patrolNum)
				}).catch(err => {
					console.log(err)
				});
			},
			layout(){
				setToken(null)
				uni.navigateTo({
				    url: 'index'
				});
			},
        }
    }
